import { defineComponent, ref } from 'vue';
import UserInfoComp from '@/components/user-info/index.vue';
import ClientQrCodeComp from '@/components/client-qr-code/index.vue';
import useHttpRequest from '@/utils/request';
import { useRoute, useRouter } from 'vue-router';
import { useStore } from 'vuex';
import { ElAvatar, ElButton, ElCard, ElMessage, ElPageHeader, ElSkeleton } from 'element-plus';
import { Star, StarFilled } from '@element-plus/icons-vue';
import { changeLtGt, formatDate, getTopicTab } from '@/utils';
export default defineComponent({
    components: {
        ElCard: ElCard,
        ElPageHeader: ElPageHeader,
        ElButton: ElButton,
        ElAvatar: ElAvatar,
        ElSkeleton: ElSkeleton,
        UserInfoComp: UserInfoComp,
        ClientQrCodeComp: ClientQrCodeComp
    },
    setup: function () {
        var route = useRoute();
        var router = useRouter();
        var state = useStore().state;
        // 获取话题数据
        var _a = useHttpRequest(), isLoading = _a.isLoading, adornUrl = _a.adornUrl, httpRequest = _a.httpRequest;
        var topic = ref();
        var getData = function () {
            httpRequest({
                url: adornUrl("/api/v1/topic/" + route.query.id),
                method: 'get',
                params: {
                    mdrender: true,
                    accesstoken: state.user.token || ''
                }
            }).then(function (_a) {
                var data = _a.data;
                data.data.content = changeLtGt(data.data.content);
                data.data.replies.forEach(function (item) {
                    item.content = changeLtGt(item.content);
                });
                topic.value = data.data;
            }).catch(function (e) {
                ElMessage.error('请求失败');
                console.error(e);
            });
        };
        getData();
        // 返回列表
        var goBack = function () {
            if (route.query.listParm) {
                router.push({
                    name: 'index',
                    params: {
                        listParm: String(route.query.listParm)
                    }
                });
            }
            else {
                router.push('/collect');
            }
        };
        // 收藏和取消收藏
        var collectHttpRequest = useHttpRequest().httpRequest;
        var collectClick = function () {
            if (topic.value && topic.value.is_collect) {
                topicDeCollect();
            }
            else {
                topicCollect();
            }
        };
        var topicCollect = function () {
            collectHttpRequest({
                url: adornUrl("/api/v1/topic_collect/collect"),
                method: 'post',
                data: {
                    topic_id: topic.value && topic.value.id,
                    accesstoken: localStorage.getItem('token') || ''
                }
            }).then(function () {
                if (topic.value)
                    topic.value.is_collect = true;
            }).catch(function (e) {
                ElMessage.error('请求失败');
                console.error(e);
            });
        };
        var topicDeCollect = function () {
            collectHttpRequest({
                url: adornUrl("/api/v1/topic_collect/de_collect"),
                method: 'post',
                data: {
                    topic_id: topic.value && topic.value.id,
                    accesstoken: localStorage.getItem('token') || ''
                }
            }).then(function () {
                if (topic.value)
                    topic.value.is_collect = false;
            }).catch(function (e) {
                ElMessage.error('请求失败');
                console.error(e);
            });
        };
        return {
            topic: topic,
            isLoading: isLoading,
            Star: Star,
            StarFilled: StarFilled,
            formatDate: formatDate,
            getTopicTab: getTopicTab,
            goBack: goBack,
            collectClick: collectClick,
        };
    },
});
